<template>
  <div>
    <div style="text-align: center">
      <span>Group Name:</span>
      <el-input
        style="width: 350px; margin-left: 4px;"
        placeholder="Please input group name"
        v-model="name"
      ></el-input>
    </div>

    <div style="display: flex; align-items: center; justify-content: center;">
      <ul class="icon-list">
        <drop
          v-for="(activeIcon, index) in activeIcons"
          :key="`active-icon-${index}`"
          class="drop"
          @drop="handleDrop"
          @dragover="handleDragover(index)"
        >
          <drag :transfer-data="activeIcon" @dragstart="dragStartIndex = index">
            <li>
              <el-popover trigger="hover" placement="top">
                <p>Name: {{ activeIcon }}</p>
                <p>Program location: ***</p>
                <div slot="reference" class="name-wrapper">
                  <i :class="activeIcon"></i>
                </div>
              </el-popover>
            </li>
          </drag>
        </drop>
      </ul>

      <div style="margin-left: 6px;">
        <div
          v-for="(icon, index) in icons"
          :key="`icon-${index}`"
          style="margin: 6px 0px;"
        >
          <drag class="drag" :transfer-data="icon">
            <i :class="icon"></i>
            {{ icon }}
          </drag>
        </div>
      </div>
    </div>

    <div style="display: flex; justify-content: center;">
      <el-button round type="success" @click="openComputersDialog"
        >Computers</el-button
      >
      <el-button round type="warning" @click="openMessageDialog"
        >Messages</el-button
      >
      <el-button round @click="$router.push('/')">Cancel</el-button>
      <el-button
        type="primary"
        :loading="processingSave"
        round
        @click="saveGroup"
        >Save</el-button
      >
    </div>

    <el-dialog title="Add Computers" :visible.sync="computerDialog" width="30%">
      <div style="display: flex; justify-content: space-around;">
        <el-card>
          <div slot="header" class="clearfix">
            <span>Active Computers</span>
          </div>
          <div style="display: flex; flex-direction: column;">
            <span
              v-for="(computer, index) in activeComputers"
              :key="`active-computer-${index}`"
            >
              {{ computer }}
            </span>
          </div>
        </el-card>
        <el-button
          icon="el-icon-d-arrow-left"
          @click="updateActiveComputers"
          round
        ></el-button>
        <el-card width="200px">
          <div slot="header" class="clearfix">
            <span>Available Computers</span>
          </div>

          <el-checkbox-group
            v-model="checkList"
            style="display: flex; flex-direction: column"
          >
            <el-checkbox
              v-for="(computer, index) in availableComputers"
              :key="`available-computer-${index}`"
              :label="computer"
            ></el-checkbox>
          </el-checkbox-group>
        </el-card>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="computerDialog = false">Cancel</el-button>
        <el-button type="primary" @click="computerDialog = false"
          >Save</el-button
        >
      </span>
    </el-dialog>

    <el-dialog
      title="Manage Messages"
      :visible.sync="messageDialog"
      width="50%"
    >
      <el-card class="box-card" style="margin-top: 10px;">
        <div slot="header" class="clearfix">
          <span>Messages</span>
          <el-button
            style="float: right; padding: 3px 0"
            type="text"
            icon="el-icon-plus"
            @click="openNewMessageDialog"
            >New message</el-button
          >
        </div>

        <el-table :data="messageTableData" style="width: 100%">
          <el-table-column label="Content" sortable>
            <template slot-scope="scope">
              {{ getShortMessage(scope.row.content) }}
            </template>
          </el-table-column>
          <el-table-column prop="activatedAt" label="Activate Date" sortable>
          </el-table-column>
          <el-table-column label="Status" sortable>
            <template slot-scope="scope">
              <el-tag
                :type="scope.row.status == 1 ? 'success' : 'danger'"
                disable-transitions
                >{{ scope.row.sttaus == 1 ? "Active" : "Deactive" }}</el-tag
              >
            </template>
          </el-table-column>
        </el-table>
      </el-card>
    </el-dialog>

    <el-dialog title="New Message" :visible.sync="newMessageDialog" width="30%">
      <div>
        <span style="margin-right: 4px;">Activation Date: </span>
        <el-date-picker
          v-model="newMessage.activatedAt"
          type="date"
          placeholder="Please pick activation date"
        >
        </el-date-picker>
      </div>
      <div>
        <span style="margin-right: 4px;">Active:</span>
        <el-switch v-model="newMessage.active"> </el-switch>
      </div>

      <el-input
        type="textarea"
        :rows="5"
        placeholder="Please input message content"
        v-model="newMessage.content"
        style="margin-top: 6px;"
      >
      </el-input>

      <span slot="footer" class="dialog-footer">
        <el-button @click="newMessageDialog = false">Cancel</el-button>
        <el-button type="primary" @click="newMessageDialog = false"
          >Save</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { Drag, Drop } from "vue-drag-drop";
import GlobalFunction from "@/mixins/GlobalFunction.js";

export default {
  components: { Drag, Drop },
  mixins: [GlobalFunction],
  data() {
    return {
      dropIndex: -1,
      icons: [
        "el-icon-platform-eleme",
        "el-icon-eleme",
        "el-icon-delete-solid",
        "el-icon-delete",
        "el-icon-s-tools",
        "el-icon-setting",
        "el-icon-user-solid",
        "el-icon-user",
      ],
      activeIcons: Array(9).fill(""),
      dragStartIndex: -1,
      processingSave: false,
      name: "",
      computerDialog: false,
      checkList: [],
      availableComputers: [
        "Computer A",
        "Computer B",
        "Computer C",
        "Computer D",
        "Computer E",
      ],
      activeComputers: [],
      messageDialog: false,
      messageTableData: [
        {
          content:
            "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum",
          activatedAt: "2020-12-22",
          status: 1,
        },
        {
          content: "Short Text",
          activatedAt: "2020-12-23",
          status: 0,
        },
      ],
      newMessageDialog: false,
      newMessage: {
        activatedAt: new Date(),
        content: "",
        active: false,
      },
    };
  },
  methods: {
    handleDrop(data) {
      let tempIcons = { ...this.activeIcons };
      if (this.activeIcons[this.dropIndex] == "") {
        tempIcons[this.dropIndex] = data;
        if (this.dragStartIndex > -1) {
          tempIcons[this.dragStartIndex] = "";
        }
      } else {
        const tmpIcn = tempIcons[this.dragStartIndex];
        tempIcons[this.dragStartIndex] = tempIcons[this.dropIndex];
        tempIcons[this.dropIndex] = tmpIcn;
      }
      this.activeIcons = tempIcons;
    },
    handleDragover(data) {
      this.dropIndex = data;
    },
    saveGroup() {
      this.processingSave = true;
      setTimeout(() => {
        this.processingSave = false;
        this.$notify({
          title: "Info",
          message: "New group is created",
          position: "bottom-right",
          type: "success",
        });
      }, 2000);
    },
    openComputersDialog() {
      console.log("openComputersDialog");
      this.computerDialog = true;
    },
    updateActiveComputers() {
      let newAvailableComputers = this.availableComputers.filter((computer) => {
        if (this.checkList.indexOf(computer) > -1) {
          this.activeComputers.push(computer);
          return false;
        }
        return true;
      });

      this.availableComputers = newAvailableComputers;
    },
    openMessageDialog() {
      this.messageDialog = true;
    },
    openNewMessageDialog() {
      this.newMessageDialog = true;
    },
  },
};
</script>

<style>
.drop.over {
  border-color: #aaa;
  background: #ccc;
}
.drop-cell {
  width: 150px;
  height: 150px;
  border: 1px solid black;
}

.icon-list {
  overflow: hidden;
  list-style: none;
  padding: 0 !important;
  border: 1px solid #b2aaaa;
  border-radius: 4px;
  width: 500px;
}

.icon-list li {
  float: left;
  width: 33.33%;
  text-align: center;
  height: 120px;
  line-height: 120px;
  color: #666;
  font-size: 32px;
  border-right: 1px solid #b2aaaa;
  border-bottom: 1px solid #b2aaaa;
  margin-right: -1px;
  margin-bottom: -1px;
}
</style>
